header {
    background-color: $slide-color;
    min-width: 250px;
    max-width: 300px;
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 5000;
    color: white;
    -webkit-transition: left ease-in-out 200ms;
    transition: left ease-in-out 200ms;
}

.header-word {
    position: relative;
}

.header-word h1 {
    text-align: center;
    margin-right: 32px;
}

.header-word svg {
    position: absolute;
    font-size: 25px;
    cursor: pointer;
    right: 10px;
    top: 18%;
}

header nav ul {
    padding: 0;
    list-style: none;
}

header nav ul li {
    margin: 20px 10px;
}

header nav ul li:hover {
    background-color: #2c3e50;
}


header nav ul li a {
    display: block;
    padding: 10px;
    text-decoration: none;
    min-width: 100%;
    color: inherit;
}

header nav ul li a svg {
    margin-right: 20px;
}


.show {
    left: 0;
}

.hide {
    left: -210px;
}


