.preview__bgColor {
    background-color: $preview-color;
}

.previewContent {
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    flex-grow: 0;
    color: white;
}

.previewContent h1 {
    font-size: 2.5em;
    margin-bottom: 0;
    word-break: break-word;
    margin-right: 20px;
    margin-left: 20px;
}

.previewContent p {
    font-size: 1.5em;
    word-break: break-word;
    margin-right: 20px;
    margin-left: 20px;
}


