.img-container {
    border: 2px solid $preview-color;
    padding-top: 20px;
    padding-bottom: 20px;
    font-size: 0.9rem;
    cursor: pointer;
}

.img-container p {
    margin-bottom: 0;
}

.clickHere {
    color: #16ab39;
    text-decoration: underline;
    font-weight: bold;
}

.imageLabel {
    margin: 0em 0em 0.28571429rem 0em;
    display: block;
}


