* {
    box-sizing: border-box;
}

html {
    height: 100%;
  }

body {
    font-family: $font-family;
    letter-spacing: 1px;
    min-height: 100%;
    position: relative;
    color: rgba(0, 0, 0, 0.87);
}

#app {
    height: 100vh;
}

.is-active {
    font-weight: bold;
    background-color: #2c3e50;
}

.overFlowPrevent {
    overflow: hidden;
}

.green {
    color: #19c8a6;
}

.red {
    color: red;
}

.virtualBody {
    margin-left: 40px;
}

.gridFullHeight {
    height: 100vh;
}

.text-center {
    text-align: center;
}


